<template>
   <div v-html="articalContent"></div>
</template>

<script setup>
import { ref } from "vue";
import axios from "axios";
const mdPath = "/md/competition.md";
const articalContent = ref("")

var MarkdownIt=require('markdown-it');
const { align } = require("@mdit/plugin-align");
const { mark } = require("@mdit/plugin-mark");
const { stylize } = require("@mdit/plugin-stylize");
var md=new MarkdownIt().use(align).use(mark).use(stylize);


axios.get(mdPath).then(res => {
    // console.log(res.data);

    articalContent.value = md.render(res.data);
});



</script>